<template>
  <app-bar/>
  <v-container
      class="py-8 px-6"
      fluid
  >
    <v-row>
      <v-col
          v-for="month in blogMonths"
          :key="month"
          cols="12"
      >
        <v-card>
          <v-list lines="two">
            <v-list-subheader>{{ month }}</v-list-subheader>
            <template v-for="blog in blogs" :key="blog.id">
              <router-link style="text-decoration: none; color: inherit;" :to="`/blog/${blog.blogID}`">
                <v-list-item>
                  <template v-slot:prepend>
                    <v-avatar color="grey-darken-1">
                      <v-img cover :src="blog.blogImage"></v-img>
                      "
                    </v-avatar>
                  </template>

                  <v-list-item-title>{{ blog.blogTitle }}</v-list-item-title>

                  <v-list-item-subtitle>
                    {{ blog.blogDescription }}
                  </v-list-item-subtitle>
                </v-list-item>
              </router-link>
              <v-divider
                  v-if="n !== 6"
                  :key="`divider-${n}`"
                  inset
              ></v-divider>
            </template>
          </v-list>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import AppBar from "@/components/app-bar-default.vue";
import dataService from "@/services/DataService";

export default {
  name: "page-blog-overview",
  components: {AppBar},
  data: () => ({
    blogs: [],
    blogMonths: [],
  }),
  mounted() {
    this.blogs = dataService.getBlogListing()
    this.blogMonths = dataService.getBlogMonth()
  }
}
</script>

