<template>
  <app-bar/>
  <v-container class="grey lighten-3">
    <v-row>
      <v-col
          cols="12"
          sm="8"
      >
        <v-img class=" rounded-lg rounded-b-0" cover max-height="200px" :src="blogPost.blogImage"/>
        <v-card
            min-height="70vh"
            class=" rounded-lg rounded-t-0"
        >
          <v-card-title> {{ blogPost.blogTitle }}</v-card-title>
          <v-card-text><span v-html="blogPost.blogContent"></span></v-card-text>
        </v-card>
      </v-col>

      <v-col
          cols="12"
          sm="4"
      >
        <v-card class="align-content-center text-center" min-height="60">
          <v-avatar class="mt-3" size="80px">
            <v-img cover :src="blogPost.authorImg" />
          </v-avatar>
          <v-card-title>Blog door: {{ blogPost.writtenBy }}</v-card-title>
          <v-card-text>{{ blogPost.datePosted }}<br></v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import AppBar from "@/components/app-bar-default.vue";
import dataService from "@/services/DataService";

export default {
  components: {AppBar},
  data: () => ({
    blogPost: {}
  }),
  mounted() {
    this.blogPost = dataService.getBlog(this.$route.params.id)
  }
}
</script>
<style>
h2 {
  font-size: 15px;
}
