import http from "@/http-common";
import blogPosts from '@/assets/blogs.json'

class DataService {
    getAllMotorcycles() {
        return http.get(`/motorcycles/`);
    }

    getBlogListing() {
        return blogPosts;
    }

    getBlog(id) {
        return blogPosts.find(blog =>
            blog.blogID === parseInt(id)
        );
    }

    getBlogMonth() {
        const uniqueMonthsYears = new Set();
        const monthNames = ["januari", "februari", "maart", "april", "mei", "juni", "juli", "augustus", "september", "oktober", "november", "december"];
        blogPosts.forEach(blog => {
            // eslint-disable-next-line
            const [day, month, year] = blog.datePosted.split("-");
            const monthName = monthNames[month - 1]
            uniqueMonthsYears.add(`${monthName}/${year}`);
        })
        return uniqueMonthsYears
    }

    getAllMotorcyclesByLicense(license) {
        return http.get(`/motorcycles/?license=` + license);
    }

    getAllMotorcyclesBySeatHeight(seatHeight) {
        return http.get(`/motorcycles/?seatHeight=` + seatHeight);
    }

    getAllMotorcyclesByBrand(brand) {
        return http.get(`/motorcycles/?brand=` + brand);
    }

    getAllMotorcyclesByType(type) {
        return http.get(`/motorcycles/?type=` + type);
    }

    getMotorcycle(id) {
        return http.get(`/motorcycles/` + id);
    }

    getBrands() {
        return http.get('/motorcycles/brands')
    }

    getTypes() {
        return http.get('/motorcycles/types')
    }

    sendMessage(message) {
        return http.post('/contact', message)
    }
}

export default new DataService()